/*----------------------------VIDEO-----------------------------------------*/

.video_container {
    height: calc(var(--vh, 1vh) * 100);
    /*height: 100vh;*/
    width: 100%;
    display: flex;
    flex-direction: column;
    /*object-fit: cover;*/

}

.main_video {
    object-fit: cover;
    /*height: 100%;*/
    width: 100%;
    /*height: 100vh;*/
    height: calc(var(--vh, 1vh) * 100);
    position: absolute;
}



.overlay {
    position: absolute;
    /*object-fit: cover;*/
/*height: 100vh;*/
    height: calc(var(--vh, 1vh) * 100);

    width: 100%;
    background: rgba(0, 0, 0, 0.25);
    z-index: 10;
}

.my-element {
    /*height: 100vh; !* Fallback for browsers that do not support Custom Properties *!*/
    height: calc(var(--vh, 1vh) * 100);
}

